import React, { Component } from 'react';
import { ResponsiveLine } from '@nivo/line';
import { observer } from 'mobx-react';
import deviceStore from '../../stores/device-store';

import './index.css';
import { LINE_PROPS } from '../../constant';

@observer
class StaticsPage extends Component {
  constructor(props) {
    super(props);
    this.lineProps = LINE_PROPS;
  }

  render() {
    return (
      <div className="page page__statics statics">
        <div className="row">
          <div className="col col-12 module">
            <div className="module__title">
              Door Distance（cm）
            </div>
            <ResponsiveLine
              data={deviceStore.distanceToTime}
              {...this.lineProps}
            />
          </div>
        </div>
        <div className="row">
          <div className="col col-12 module">
            <div className="module__title">
              NB Signal Strength（dBm）
            </div>
            <ResponsiveLine
              data={deviceStore.signalToTime}
              {...this.lineProps}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default StaticsPage;
